import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MyTest from '@/components/MyTest'
import MyApp from '@/components/MyApp'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/mytest',
      name: 'mytest',
      component: MyTest
    },
    {
      path: '/app/:appname',
      name: 'myapp',
      component: MyApp
    }
  ]
})



